<template>
  <div>
    <fTitle>
      <template #title>Dialog 对话框</template>
      <template #introduce>在保留当前页面状态的情况下，告知用户并承载相关操作。</template>
    </fTitle>
    <sTitle>
      <template #title>自定义内容</template>
      <template #introduce>Dialog 组件的内容可以是任意的，甚至可以是表格或表单，下面是应用了 Element Table 和 Form 组件的两个样例。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content">
  <el-button type="text" @click="dialogVisible = true">点击打开 Dialog</el-button>
<el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="30%"
  :before-close="handleClose">
  <span>这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
</div>
    </el-col>
    <el-col :span="10">
<div class="grid-content">
  <el-button type="text" @click="outerVisible = true">点击打开外层 Dialog</el-button>

  <el-dialog title="外层 Dialog" :visible.sync="outerVisible">
    <el-dialog
      width="30%"
      title="内层 Dialog"
      :visible.sync="innerVisible"
      append-to-body>
    </el-dialog>
    <div slot="footer" class="dialog-footer">
      <el-button @click="outerVisible = false">取 消</el-button>
      <el-button type="primary" @click="innerVisible = true">打开内层 Dialog</el-button>
    </div>
  </el-dialog>
</div></el-col></el-row>
<el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content"><el-button type="text" @click="dialogTableVisible = true">打开嵌套表格的 Dialog</el-button>

<el-dialog title="收货地址" :visible.sync="dialogTableVisible">
  <el-table style="height: 500px;overflow: scroll;" :data="goodList">
    <el-table-column property="category_id" label="种类id" width="150"></el-table-column>
    <el-table-column property="name" label="大类名称" width="200"></el-table-column>
    <el-table-column  label="图片">
      <template slot-scope="scope">
         <el-image style="width: 20%;height: 100%;"  :src="scope.row.image.preview_url"></el-image>
      </template>
    </el-table-column>
  </el-table>
</el-dialog>

<!-- Form -->
</div>
    </el-col>
    <el-col :span="10">
<div class="grid-content">
  <el-dialog title="收货地址" :visible.sync="dialogFormVisible">
  <el-form :model="form">
    <el-form-item label="活动名称">
      <el-input v-model="form.name" style="display: inline-block;" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="活动区域">
      <el-select v-model="form.region" placeholder="请选择活动区域">
        <el-option label="区域一" value="shanghai"></el-option>
        <el-option label="区域二" value="beijing"></el-option>
      </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
  </div>
</el-dialog>
<el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
import request from '@/utils/request'
export default {
  name: 'ColorIndex',
  data () {
    return {
      goodList: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      dialogVisible: false,
      outerVisible: false,
      innerVisible: false
    }
  },
  methods: {
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    }
  },
  async created () {
    const goodsList = await request.get('/category/list')
    this.goodList = goodsList.data.list[0].children
    console.log(goodsList.data.list[0].children)
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;

 .el-color-picker__icon, .el-input, .el-textarea {
    display: inline-block;
     width: 26%;
}
      }
    }}
</style>
